import ___CSS_LOADER_API_IMPORT___ from "../../../../../../node_modules/css-loader/dist/runtime/api.js";
var ___CSS_LOADER_EXPORT___ = ___CSS_LOADER_API_IMPORT___(___CSS_LOADER_API_SOURCEMAP_IMPORT___);
// Module
___CSS_LOADER_EXPORT___.push([module.id, ".tox-tinymce-aux{z-index:9000!important}", "",{"version":3,"sources":["webpack://./resources/admin/js/views/articles/components/form.vue"],"names":[],"mappings":"AAkJA,iBACA,sBACA","sourcesContent":["<template>\n    <el-card class=\"box-card\">\n    <el-form :model=\"form\" label-position=\"top\">\n        <el-tabs type=\"card\" v-model=\"activeTab\">\n            <el-tab-pane label=\"Основная информация\" name=\"main\">\n                <div class=\"d-flex\">\n                    <el-form-item prop=\"logo\" label=\"Превью\" style=\"margin-right: 20px\">\n                        <el-upload\n                            action=\"\"\n                            v-model=\"form.preview\"\n                            class=\"avatar-uploader\"\n                            :auto-upload=\"false\"\n                            :show-file-list=\"false\"\n                            :on-change=\"uploadImage\"\n                        >\n                            <figure class=\"avatar\" v-if=\"form.preview\">\n                                <img  :src=\"form.preview\" >\n                            </figure>\n                            <i v-else class=\"el-icon-plus avatar-uploader-icon\"></i>\n                        </el-upload>\n                    </el-form-item>\n                    <div style=\"flex:1\">\n                        <el-form-item prop=\"title\" label=\"Название\" :error=\"errors.get('title')\">\n                            <el-input v-model=\"form.title\"/>\n                        </el-form-item>\n                        <el-form-item prop=\"slug\"\n                                      label=\"Псевдоним (Если оставить поле пустым будет сгенерировано на основе названия статьи)\"\n                                      :error=\"errors.get('slug')\">\n                            <el-input v-model=\"form.slug\"/>\n                        </el-form-item>\n                        <div class=\"row\">\n                            <el-form-item class=\"col-md-6\"\n                                          prop=\"date\" label=\"Дата\"\n                                          :error=\"errors.get('date')\">\n                                <el-date-picker\n                                    v-model=\"form.date\"\n                                    type=\"date\"\n                                    format=\"dd-MM-yyyy\"\n                                    value-format=\"yy-MM-dd\"\n                                    placeholder=\"Выберите дату\">\n                                </el-date-picker>\n                            </el-form-item>\n                            <el-form-item label=\"Опубликован\" class=\"col-md-5\"  prop=\"published\">\n                                <el-checkbox v-model=\"form.published\">Опубликован</el-checkbox>\n                            </el-form-item>\n                        </div>\n\n                        <el-form-item prop=\"text\" label=\"Текст\">\n                           <tinymce v-model=\"form.content.text\"/>\n                        </el-form-item>\n\n                    </div>\n                </div>\n            </el-tab-pane>\n            <el-tab-pane label=\"Настройки и SEO\" name=\"config\">\n                <Seo :form = \"form.seo\" v-if=\"form.seo\"></Seo>\n            </el-tab-pane>\n            </el-tabs>\n            <el-button type=\"success\" @click.prevent=\"submitForm\">Сохранить</el-button>\n        </el-form>\n    </el-card>\n</template>\n<script>\n\nimport { Errors } from  '@/common/js/services/errors.js';\nimport Seo from '@/admin/js/components/seo/seo';\nimport Tinymce from '@/admin/js/components/Tinymce';\n    export default {\n        components: {\n            'Seo':Seo,\n            Tinymce\n        },\n        props: {\n            saveMsg: {\n                default: 'Статья сохранена'\n            },\n            form: {\n                type:Object,\n            },\n            formAction: {\n                type:Object\n            },\n        },\n        data() {\n            return {\n                activeTab:'main',\n                errors: new Errors(),\n            }\n        },\n        methods: {\n            submitForm() {\n                axios({\n                    method: this.formAction.method ,\n                    url: this.formAction.url,\n                    data: this.form\n                }).then((response) => {\n                    this.$message({\n                        message: this.saveMsg,\n                        type: 'success'\n                    });\n                    this.$emit('submit', response.data);\n                })\n                    .catch((error) => {\n                        this.errors.record(error.response.data.errors);\n                    })\n                ;\n\n            },\n            uploadImage(file){\n                let cond = this.beforeImageUpload(file.raw);\n                this.form.imageName = file.raw.name;\n                console.log(cond);\n                if(cond){\n                    this.createImage(file);\n                }\n            },\n\n            beforeImageUpload(file) {\n                const isJPG = file.type === 'image/jpeg';\n                const isPNG = file.type === 'image/png';\n                const isLt2M = file.size / 1024 / 1024 < 2;\n\n                if (!isJPG && !isPNG) {\n                    this.$message.error('Картинка должна быть в формате jpeg или png');\n                }\n                if (!isLt2M) {\n                    this.$message.error('Размер не может превышать 2МБ');\n                }\n                return (isJPG || isPNG) && isLt2M;\n            },\n\n            createImage(file) {\n                let reader = new FileReader();\n                let vm = this;\n                reader.onload = (e) => {\n                    vm.form.preview = e.target.result;\n                };\n                reader.readAsDataURL(file.raw);\n            },\n        },\n        mounted() {\n\n        }\n    }\n</script>\n<style>\n.tox-tinymce-aux {\n    z-index: 9000 !important;\n}\n</style>\n"],"sourceRoot":""}]);
